<template>
  <div>
    <el-row class="wrapper">
      <el-col class="left-placeholder hidden-sm-and-down" :span="22">
      </el-col>
      <el-col class="right-placeholder" :span="2" :xl="2" :lg="2" :md="2" :sm="24" :xs="24">

        <div class="text-center">
          <img class="logo" src="data:image/svg+xml;base64," alt="logo">
          <h3 class="title"> {{ kindo.config.theme.title }} </h3>
        </div>

        <el-form ref="form" :model="view.model" :rules="view.rules" @submit.native.prevent @keyup.enter.native="login">
          <el-form-item label="" prop="userName">
            <el-input v-model="view.model.userName" prefix-icon="el-icon-date" autofocus placeholder="Username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model="view.model.password" prefix-icon="el-icon-date" placeholder="Password"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-col class="text-left" :span="12">
              <el-checkbox v-model="view.model.rememberMe">记住我</el-checkbox>
            </el-col>
            <el-col class="text-right" :span="12">
              <el-button type="text" @click="rememberPassword">忘记密码？</el-button>
            </el-col>
          </el-form-item>
          <el-form-item label="">
            <el-button class="btn-login" round type="primary" @click="login">登 录</el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col class="bottom-placeholder">
        {{ kindo.config.business.copyright }}
        <a target="_blank" :href="kindo.config.business.businessSite"> {{ kindo.config.business.businessName }} </a>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import 'element-ui/lib/theme-chalk/display.css'

import { mapActions } from 'vuex'

export default {
  data() {
    return {
      view: {
        model: {
          userName: '',
          password: '',
          rememberMe: false
        },

        rules: {
          userName: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
          password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
        }
      }
    }
  },

  methods: {
    ...mapActions('USER', ['SET']),

    login() {
      this.$refs.form.validate(valid => {
        if (valid) {
          this.$router.push('/layout')

          // Get user info from server, set to vuex
          this.SET({
            userName: '这是 login 处得到的用户信息'
          })
        }
      })
    },

    rememberPassword() {
      this.$notify.info({
        title: '提示',
        dangerouslyUseHTMLString: true,
        message: '<strong>请联系 <i>管理员</i> 或 <i>运维人员</i> 重置密码</strong>'
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.wrapper {
  display: flex;
  min-height: 100vh;

  .left-placeholder {
    background: url(./assets/image/login-bg.jpg) center center;
    background-size: cover;
    flex: auto;
    color: rgba(255, 255, 255, 0.8);
    position: relative;
    align-items: flex-end;
    display: flex;

    &:before {
      content: '';
      display: block;
      background-color: rgba(0, 0, 0, 0.6);
      height: 100%;
      width: 100%;
      position: absolute;
      top: 0px;
      left: 0px;
      z-index: 0;
    }
  }

  .right-placeholder {
    min-width: 480px;
    background-color: #fff;
    padding: 5vw;

    .text-center {
      .logo {
        width: 8vw;
        margin-bottom: 2vh;
      }

      .title {
        font-size: 22px;
        margin-bottom: 8vh;
      }
    }

    .el-button.is-round.btn-login {
      border-radius: 5px;
      width: 100%;
    }
  }

  .bottom-placeholder {
    position: fixed;
    bottom: 1vh;
    left: 1vw;
    font-size: 14px;
    color: rgba(255, 255, 255, 0.375);
    margin-bottom: 0;

    a {
      transition: color 0.2s ease-in-out, border-bottom-color 0.2s ease-in-out;
      color: inherit;
      border-bottom: dotted 1px;
      text-decoration: none;
    }
  }

  @media (max-width: 992px) {
    .bottom-placeholder {
      color: rgba(0, 0, 0, 0.375);
    }
  }
}
</style>
